<template>
  <div id="app">
    <div id="nav">
      <router-link to="/">Profile</router-link> |
      <span @click="logout">Logout</span>
    </div>
    <router-view/>
  </div>
</template>

<script>
import { makeHttpRequest } from "@/utils/httpUtils";
export default {
  name:"App",
  methods:{
    logout(){
      makeHttpRequest({ url: "/auth/logout", method: "POST" ,params:"serurl=/accounts" })
        .then(this.loggedOut)
        .catch(this.parseError);
    },
    loggedOut(response){
       window.location.href=response.redirect
    }
  }
}
</script>


