<template>
  <div class="home">
    <img alt="Azito" src="../assets/logo.svg" />
    <form method="post" @submit.prevent="submitProfile">
      <input
        type="text"
        placeholder="Display Name"
        v-model="form.displayName"
      />
      <input type="username" placeholder="User Name" v-model="form.userName" />
      <input type="password" placeholder="Password" v-model="form.password" />
      <div>
          <input class="redBtn" type="submit" value="Save Profile" />
      </div>
      
    </form>
    <div>
        <button @click="closeAccount" v-text="'Close Account'" />
    </div>
  </div>
</template>

<script>
import { makeHttpRequest } from "@/utils/httpUtils";
export default {
  name: "profile",
  data() {
    return {
      form: {
        displayName: "",
        userName: "",
        password: ""
      }
    };
  },
  mounted() {
    this.init();
    this.initWebSocket();
  },
  methods: {
    init() {
      makeHttpRequest({ url: "/api/v1/users/me", method: "GET" })
        .then(this.parseUser)
        .catch(this.parseError);
    },
    initWebSocket() {
      const url = "ws://hari:3000";
      const connection = new WebSocket(url);
      connection.onopen = () => {
       console.log("websocket open")
      };
      connection.onerror = error => {
        console.log(`WebSocket error: ${error}`);
      };
      connection.onmessage = e => {
        console.log(`WebSocket Message: ${e.data}`);
        let message=JSON.parse(e.data)
        if (message.from == "IAM" && message.code=="IAM_2") {
          window.location.href = "/login"
        }
      }
    },
    parseUser(response, status, requestObject) {
      this.form.userName=response.userName
       this.form.displayName=response.displayName
    },
    parseError(response, status, requestObject) {
      window.location.href="/login"
    },
    submitProfile(){
      makeHttpRequest({ url: "/api/v1/users/me", method: "POST",params:"displayName="+this.form.displayName,contentType: "application/x-www-form-urlencoded;charset=UTF-8" })
        .then(this.parseUser)
        .catch(this.parseUser);
    },
    closeAccount(){
      makeHttpRequest({ url: "/api/v1/users/me", method: "DELETE",params:"password="+this.form.password,contentType: "application/x-www-form-urlencoded;charset=UTF-8" })
        .then(this.accountClosed)
        .catch(this.parseUser);
    },
    accountClosed(){
      // window.location.href="/login"
    }
  }
};
</script>
