import Vue from 'vue'
import Router from 'vue-router'
import Profile from './views/Profile.vue'
import NotFound from '@/views/NotFound'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'profile',
      component: Profile
    },
    {
      path: '*',
      component:NotFound
    }
  ]
})
